@font-face{
  src: url(./Bagel_Fat_One/BagelFatOne-Regular.ttf);
  font-family: 'Bagel' ;
  font-weight: normal;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html{
  font-size: 10px;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  color: white;
  font-family: 'Bagel';
  overflow-y: hidden;
  background:  linear-gradient(#954A25 3%, #F98B54, #954A25 97%) no-repeat;
  height: 120vh;
  width: 100vw; 
  max-width: 100vw;
}

