.App {
  text-align: center;
  display: grid;
  grid-template-rows: 1fr 4fr 0.5fr 2fr;
}

.cookie{
  color: #f0cd87;
}

.title{
  font-size: 5.2rem;
  margin-top: 3rem;
}


.clicker{
  align-self: center;
  padding: 0 5rem;
}

.clicker--img{
  width: 100%;
  aspect-ratio: 1/1;
  animation-name: spin;
  animation-duration: 100s;
  animation-iteration-count: infinite;
}

.clicker--img:active{
  width: 97%;
}

.clicker--count{
  font-size: 4rem;
  margin-top: 2rem;
  max-width: 100%;
  overflow-y: auto;
}

.menu{
  align-self: center;
  display: flex;
  justify-content: center;
}

.menubutton{
  background-color: #D4944D;
  border-radius: 10px;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  padding: 2rem 0;
  margin: 0 2%;
  height: 9rem;
  width: 25%;
  max-width: 7rem;
  border: none;
}

.menubutton--icon{
  color: white;
  font-size: 4rem;
  margin: 0 auto;
}

.popup{
  display: flex;
  align-items: center;
  flex-direction: column;
  background-color: #D4944D;
  z-index: 11;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -59%);
  border-radius: 20px;
  height: fit-content;
  min-height: 65%;
  padding-bottom: 1rem;
  width: 90vw;
}

.popup.store{
  display: grid;
  grid-template-rows: 0.4fr 0.3fr 4fr 0.5fr;
}

.popup.about{
  justify-content: space-between;
}

.popup.about a{
  font-size: 30px;
  background-color: #954A25;
  border-radius: 20px;
  color: white;
  padding: 4%;
}

.about--text{
  font-size: 30px;
  text-align: center;
}

.popup--title{
  margin: 0 auto;
  color: #1C1B1F;
  font-size: 5rem;
  padding: 0.2em 0;
}

.store__choose{
  display: flex;
  justify-content: space-around;
}

.store__choose button{
  background-color: #AF7B43;
  border: none;
  border-radius: 5px;
  color: white;
  font-family: 'Bagel';
  padding: 2% 4%;
  font-size: 2rem;
}

.store__choose button.dark{
  background-color: #954A25;
}

.popup--close{
  justify-self: center;
  color: white;
  font-size: 3rem;
  font-family: 'Bagel' ;
  border: none;
  background: #49331C;
  width: 30%;
  border-radius: 20px;
  padding: 1px;
}

.upgrademenu{
  padding: 3% ;
}

.card{
  padding: 1%;
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  grid-column-gap: 1% ;
  width: 100%;
  justify-content: center;
  margin: 2% 0;
  background: #AF7B43;
  border-radius: 10px;
}

.card--title{
  font-size: 18px;
}

.card--img{
  width: 100%;
  border-radius: 10px;
}

.card__numbers{
  display: flex;
  justify-content: space-around;
}

.card--button{
  background: none;
  color: white;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.25);
  font-size: 3rem;
  border: none;
}

.popup.options{
  display: grid;
  grid-template-rows: 1fr 4fr 1fr;
}

.options--input{
  font-size: 30px;
  margin: 5rem;
  
}

.options--input input{
  margin-left: 2rem; 
  -webkit-appearance: none;
  background: left linear-gradient(#AF7B43, #49331C) ;
  padding: 1rem;
  border-radius: 20px;
  
}

.skins{
  display: grid;
}

.skin__choose{
  display: flex;
  align-items: center;
}

.skin__choose--arrow{
  margin: 0 1rem;
  width: 4rem;
  height: fit-content;
  padding: 2rem 0;
  font-size: 20px;
  color: white;
  border: none;
  border-radius: 20px;
  background-color: #49331C;
}

.skin__choose--img img{
  width:26rem;
}


/* .options--input input::-webkit-slider-thumb{
  width: 203px;
} */

@media (min-width: 520px){
  .clicker--img{
    width: 70%;
  }

  .popup{
    width: 70vw;
  }
}

@media (min-width: 1020px){
  html{
    font-size: 18px;
  }

  .App{
    grid-template-rows: auto;
    grid-template-columns: repeat(3, 1fr);
  }
  
  .title{
    font-size: 4rem;
    margin: 0 0 4rem 0;
  }

  .popup{
    position: relative;
    width: 33%;
    transform: translate(-50%, -130%);
    min-height: 20rem ;
  }

  .popup.options{
    grid-template-rows: 1fr 2fr 1fr;
  }

  .clicker--img:active{
    width: 67%;
  }

  .column1{
    margin-top: 10%;
    display: flex;
    flex-direction: column;
  }

  .column1--title{
    font-size: 3rem;
  }

  .column2{
    display: grid;
    
  }

  .menubutton{
    padding: 0;
    width: 33%;
    height: 3rem;
  }

  .menubutton--icon{
    font-size: 2rem;
    width: 50%;
  }
}